import {Time} from '../index'

export const config = {
  // 
  startTime: {
    earliest: new Time(8, 30),
    latest: new Time(9, 0)
  },
  // 
  endTime: {
    earliest: new Time(18, 0),
    latest: new Time(18, 30)
  },
  workingTime: 8 * 60,
  // 
  lunchBreak: {
    start: new Time(12, 0),
    end: new Time(13, 30),
    time: 90
  },
  flexTime: 60,
  // 
  overtime: {
    //  
    min: 60,
    //  
    step: 30,
    // 
    includeWorkday: true
  },
  // 
  leave: {
    //  
    min: 60,
    //  
    step: 60,
    // 
    includeWorkday: true
  }
}



